<template>
  <div>
    <!-- 123 -->
    <div style="width:100px;height:50px">
      <button style="float:left;height:30px;margin-top:10px;padding:5px;"  @click="btn2">减</button>
      <span  style="float:left;width:30px;height:30px;line-height:30px;margin-top:10px;text-align:center;">{{num}}</span>
      <button style="float:left;height:30px;margin-top:10px;padding:5px;" @click="btn1">加</button>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      num:0
    }
  },
  methods:{
    btn1(){
      this.num++
    },
    btn2(){
      this.num--
      if( this.num<=0){
        this.num=0
      }
    }
  }
};
</script>

