import Vue from 'vue'
import VueRouter from 'vue-router'
import no1 from '../p/no1.vue'
import no2 from '../p/no2.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'no1',
    component: no1
  },
  {
    path: '/no2',
    name: 'no2',
    component: no2
  }
]

const router = new VueRouter({
  routes
})

export default router
